<template>
  <div id="app">
    <div id="map" style="width: 100%; height: 500px;"></div>
    <canvas id="elevationChart" width="400" height="200"></canvas> <!--     -->
  </div>
</template>

<script setup>
/* global kakao */
import { onMounted, ref } from 'vue';
import * as toGeoJSON from '@mapbox/togeojson';
import Chart from 'chart.js/auto'; // Chart.js   

const map = ref(null); //  
let elevationData = []; //    

onMounted(() => {
  initializeMap(); // Vue    
});

function initializeMap() {
  // Kakao  API  
  const script = document.createElement('script');
  script.onload = () => kakao.maps.load(createMap); //    createMap 
  script.src =
    'https://dapi.kakao.com/v2/maps/sdk.js?appkey=333bda7da18df138fb4d9b3e5cf351c4&autoload=false&libraries=services';
  document.head.appendChild(script);
}

function createMap() {
  const container = document.getElementById('map'); //   div
  const options = {
    center: new kakao.maps.LatLng(37.60255081802746, 126.946665543971804), //     
    level: 5, //   
  };
  map.value = new kakao.maps.Map(container, options); //  

  map.value.setMapTypeId(kakao.maps.MapTypeId.SKYVIEW);

  //   GPX     
  loadGPXFromServer('/gpx/.gpx');
}

//  GPX   
async function loadGPXFromServer(url) {
  try {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const gpxText = await response.text(); // GPX    
    const parser = new DOMParser();
    const gpxDOM = parser.parseFromString(gpxText, 'application/xml');

    const geojson = toGeoJSON.gpx(gpxDOM); // GPX  GeoJSON 

    drawGPXPath(geojson); // GPX   
    drawElevationChart(); //     
  } catch (error) {
    console.error('GPX     :', error);
  }
}

// GPX    
function drawGPXPath(geojson) {
  geojson.features.forEach((feature) => {
    if (feature.geometry && feature.geometry.coordinates) {
      const path = feature.geometry.coordinates.map((coord) => {
        elevationData.push(coord[2] || 0); //    (   0 )
        return new kakao.maps.LatLng(coord[1], coord[0]);
      });

      //  () 
      const polyline = new kakao.maps.Polyline({
        path: path, //  
        strokeWeight: 5, //  
        strokeColor: '#FF0033', // 
        strokeOpacity: 0.85, //  
        strokeStyle: 'solid', //  
      });

      polyline.setMap(map.value); //   
    }
  });
}

//    
function drawElevationChart() {
  const ctx = document.getElementById('elevationChart').getContext('2d');
  const labels = elevationData.map((_, index) => `${index + 1} km`); //  x  (km  )
  
  new Chart(ctx, {
    type: 'line',
    data: {
      labels: labels, // x ()
      datasets: [
        {
          label: ' (m)',
          data: elevationData, // y ( )
          borderColor: 'rgba(75, 192, 192, 1)',
          borderWidth: 2,
          fill: false,
        },
      ],
    },
    options: {
      responsive: true,
      scales: {
        x: {
          title: {
            display: true,
            text: ' (km)',
          },
        },
        y: {
          title: {
            display: true,
            text: ' (m)',
          },
        },
      },
    },
  });
}
</script>

<style scoped>
#map {
  height: 100%;
}

canvas {
  margin-top: 20px;
}
</style>
